$tradepageSvgIconImgPath : "../../img/tradepage/svg";

.latesttransactions{

  &-box{
    width: 100%;
    border: 1px solid transparentize($tradepage-color-light-stale-gray, 0.70);
    padding: 10px 15px;
  }

  &-area{}

  &-contentbox{
    color: $tradepage-color-dark-blue;
    padding: 16px;
    border: 1px solid transparentize($tradepage-color-light-stale-gray, 0.7);

    div[class*="boxtitle"]{margin-bottom: 0; padding-bottom: 10px;}
    .colorgreen{color: $tradepage-color-green;}
    .colorred{color: $tradepage-color-red;}

    > .row{
      position: relative;

      &:before{
        content: "";
        position: absolute;
        width: 1px;
        height: calc(100% - 0);
        background-color: transparentize($tradepage-color-light-stale-gray, 0.7);
        top: 0;
        left: 50%;
      }
    }
  }

  &-table{
    color: $tradepage-color-dark-blue;
    margin: 0;

    td, th{
      border-color: transparentize($tradepage-color-light-stale-gray, 0.7);
      vertical-align: middle;
      font-size: 15px;
      font-weight: normal;
      line-height: 1;
      padding: 0 5px;
      text-align: left;

      &:first-child{
        padding-left: 0;
      }
    }

    thead th{
      font-size: 17px;
      border-top-width: 0;
      border-bottom-width: 1px;
      border-color: transparentize($tradepage-color-light-stale-gray, 0.7);
      padding-top: 3px;
      padding-bottom: 3px;

      &:last-child{
        width: 20%;
      }
    }

    tbody tr:first-child td{
      padding-top: 17px;
    }

    tbody td{
      border: none;
      padding-top: 8px;
      padding-bottom: 8px;
      line-height: 0.6;
    }

    &-status{
      width: 10%;
    }
    &-type{
      width: 10%;
    }
    &-method{
      width: 10%;
    }
    &-date{
      width: 10%;
    }
    &-hour{
      width: 10%;
    }
    &-unit{
      width: 10%;
    }
    &-amount{
      width: 10%;
    }
    &-draw{
      width: 23%;
    }
    &-code{
      width: 15%;
    }

    .colororange{color: $tradepage-color-orange;}
    .colorblue{color: $tradepage-color-blue;}
    .colorgreen{color: $tradepage-color-green;}
    .colorred{color: $tradepage-color-red;}

  }

  .tableicon{
    display: inline-block;
    margin-left: 2px;
    position: relative;
    bottom: -2px;
    i{
      background-color: transparent;
      background-repeat: no-repeat;
      background-position: center center;
      display: block;
      cursor: pointer;
      width: 8px;
      height: 4px;
      margin: 1px 0;
      padding: 3px 0;
    }
    &-arrowup{
      background-image: url(#{$tradepageSvgIconImgPath}/Arrow_Up.svg);
    }
    &-arrowdown{
      background-image: url(#{$tradepageSvgIconImgPath}/Arrow_Down.svg);
    }
  }




}